





import {Component, Prop, Vue} from 'vue-property-decorator';

enum TypeColor{
  'outline',
  'outline-black',
  'outline-green',
  'outline-blue',
  'outline-red',
  'background-grey',
  'background-green',
  'background-blue',
  'background-yellow'
}

@Component
export default class Chip extends  Vue {
  @Prop(String) readonly title: string | undefined;
  @Prop({default: TypeColor.outline, type: String}) readonly typeColor: TypeColor | undefined;

  @Prop({default: 12, type: Number}) readonly sizeLetter: number | undefined;



}
