





























































































































import { Component, Vue } from 'vue-property-decorator';
import {HttpService} from "@/services/ApiService";
import {Accounting, Dboy, Vehicle, Wallet} from "@/models/service";


import Chip from "@/components/chip.vue";
@Component({
  components:{
    Chip
  }
})
export default class AccountingDetail extends Vue {
  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';

  public repartidor: Dboy| null = null;

  public errors = {};

  public isLoading = true



  async getPagarTodo() {
    await HttpService.get(`contabilidad/${this.$route.params.id}/pagartodo?filter[id]=${this.$route.params.id}&include=accountings_sum_total,accountings,accountings.users` ,
        this.setRepartidor)
  }


  async getPagar(id: number) {
    await HttpService.get(`contabilidad/${id}/pagar?filter[id]=${this.$route.params.id}&include=accountings_sum_total,accountings` ,
        this.setRepartidor)
  }




  async getRepartidor() {
    await HttpService.get(`contabilidad/mensajeros?filter[id]=${this.$route.params.id}&include=accountings_sum_total,accountings,accountings.service` ,
        this.setRepartidor)
  }

  setAccounting(data:any){
    let acc: Accounting = data.data;
    let index = this.repartidor?.accountings?.findIndex(e => e.id == acc.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, delivery)
      this.$set(this.repartidor?.accountings!, index, acc)
    }
  }



  setRepartidor(data: any){
    console.log(data)
    this.repartidor = data.data[0];
  }

  get listPaid(){
    return this.repartidor?.accountings?.filter(e => e.paid == 1);
  }
  get listNotPaid(){
    return this.repartidor?.accountings?.filter(e => e.paid == 0);
  }

  async created(){
    await this.getRepartidor();
    this.isLoading =false;


  }

}
